@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@700&family=Readex+Pro&family=Roboto:wght@400;700&display=swap);
.page{display:grid;grid-template:auto / 1fr 400px;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;min-height:100vh}.page-content{padding:20px}.sidebar{padding:20px;border-left:1px solid lightgray}

.movies{display:grid;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:20px;gap:20px}

.card-image img{object-fit:contain}

.newMovie__form{display:flex;flex-direction:column;grid-gap:10px;gap:10px;font-family:"Readex Pro", sans-serif}.form__button{height:40px;border-radius:10px;border:1px solid transparent;font-family:"Readex Pro", sans-serif;font-weight:bold;color:#fff;background-color:#839d83;transition:0.3s}.form__button:hover{background-color:#688668;cursor:pointer}

